.select {
    padding: 10px;
    font-size: 16px;
    border: 2px solid #510485;
    border-radius: 15px;
    background-color: #f8f8f8;
    color: #510485;
    cursor: pointer;
    outline: none;
    transition: border-color 0.3s ease;
}

.select:hover {
    border-color: #ff7f50;
}

.select:focus {
    border-color: #ff7f50;
}

.select::-ms-expand {
    display: none;
}

.select::after {
    content: "\25BC";
    position: absolute;
    top: 50%;
    right: 10px;
    transform: translateY(-50%);
    pointer-events: none;
    color: #510485;
}

li {
    list-style-type: none;
}
