.currentProduct {
    border: 2px solid #510485;
    border-radius: 20px;
    padding: 10px 20px;

}

.main {
    display: flex;
    flex-direction: column;
    align-items: center;
    padding: 10px 30px;
    gap: 15px;
}

.listProducts {
    display: flex;
    flex-direction: column;
    gap: 15px;
    width: 100%;
}

.inputsFilters {
    display: flex;
    gap: 15px;
}

.filters {
    display: flex;
    gap: 15px;
    align-items: center;
}

.filters span {
    border: 2px solid #510485;
    border-radius: 15px;
    padding: 7px 15px;
}

.error {
    display: flex;
    flex-direction: column;
    align-items: center;
    gap: 10px;
}